body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.hero-slider div {
  height: 40vh;
}
.slick-slide div div {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-slider div#one {
  background-image: url(/react_mega_menu/static/media/HOMEPAGE_SLIDER_IMAGE.88f4473e.jpg);
}
.hero-slider div#two {
  background-image: url(/react_mega_menu/static/media/HOMEPAGE_SLIDER_IMAGE_FLIPPED.666aacb1.jpg);
}
@media( min-width: 768px ) {
  .hero-slider div {
    height: 100vh;
  }
}
#main-navigation {
  position: fixed;
  display: flex;
  color: #fff;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 18px); 
  top: 0;
  padding: 18px;
  background-color: rgba(255,255,255,0);
  z-index: 999;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
#main-navigation:hover {
  background-color: rgba(255,255,255,1);
  color: #000;
}
#main-navigation:hover a {
  color: #000;
}
#main-navigation a {
  color: #fff;
  text-decoration: none;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
#main-navigation > div {
  flex: 1 1;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
#main-navigation > #PrimaryNav {
  flex: 7 1;
}
#main-navigation .logo {
  height: 36px;
  background-image: url(/react_mega_menu/static/media/logo_white.8078e52f.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}
#main-navigation:hover .logo {
  background-image: url(/react_mega_menu/static/media/logo_black.8386e2f2.png);
}
#main-navigation .search {
  display: inline;
  height: 1.25em;
  line-height: 1.5em;
  background-image: url(/react_mega_menu/static/media/icon_search_white.c0bb1919.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}
#main-navigation:hover .search {
  background-image: url(/react_mega_menu/static/media/icon_search.52733500.svg);
  cursor: pointer;
}
#main-navigation .search:hover,
#main-navigation .account:hover,
#main-navigation .cart:hover {
  opacity: 0.5;
}
